/**
 * dette er logikken som hndterer plogget og utlogget tilstand p nettstedet.
 * sender en bruker til ploggingssiden hvis enten kten er ferdig og/eller det finnes ikke et token i local storage
 * det gjr ogs det motsatte, s hvis det er et token, blir brukeren automatisk logget p og sendt til hjemmesiden
 */
import React, { createContext, useContext, useState, useEffect } from "react";

const AuthContext = createContext();

export const useAuth = () => useContext(AuthContext);

export const AuthProvider = ({ children }) => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  useEffect(() => {
    // Denne funksjonen sjekker tokenet i local storage for  oppdatere ploggingsstatus
    const checkAuthStatus = () => {
      const token = localStorage.getItem("token");
      setIsLoggedIn(!!token);
    };

    checkAuthStatus();
    window.addEventListener("storage", checkAuthStatus); // Listen to storage changes

    return () => {
      window.removeEventListener("storage", checkAuthStatus); // Cleanup listener
    };
  }, []);

  const login = (token) => {
    localStorage.setItem("token", token);
    setIsLoggedIn(true);
  };

  const logout = () => {
    localStorage.removeItem("token");
    setIsLoggedIn(false);
  };

  return (
    <AuthContext.Provider value={{ isLoggedIn, login, logout }}>
      {children}
    </AuthContext.Provider>
  );
};
